@use 'globals';

.a-label-question {
    padding: 0;
    margin: 0;
}

.focused {
    * {
        outline: none;
    }

    .a-label-postlabel {
        border: 1px solid globals.$theme-primary;
        border-left-color: globals.$borders;
    }

    .a-label-prelabel {
        border: 1px solid globals.$theme-primary;
        border-right-color: globals.$borders;
    }
}

.m-input-singlelineedit {
    .a-label-postlabel {
        display: inline-block;
        border: 1px solid globals.$borders;
        border-radius: 0 globals.$border-radius globals.$border-radius 0;
        position: relative;
        left: -.5em;
        background-color: globals.$background;
        padding: 10px 16px;
        color: globals.$pre-post-text;
        white-space: nowrap;
    }

    .a-label-prelabel {
        display: inline-block;
        border: 1px solid globals.$borders;
        border-radius: globals.$border-radius 0 0 globals.$border-radius;
        background-color: globals.$background;
        padding: 10px 16px;
        color: globals.$pre-post-text;
        white-space: nowrap;
    }
}

.o-question-hnumberslider {

    .a-label-prelabel {
        position: absolute;
        left: 0;
        bottom: 0;
        color: globals.$theme-primary;
    }

    .a-label-postlabel {
        position: absolute;
        right: 0;
        bottom: 0;
        color: globals.$theme-primary;
    }

    .active {
        .a-label-value {
            visibility: visible;
        }
    }

    .a-label-value {
        box-sizing: border-box;
        color: globals.$titles;
        position: absolute;
        top: -25px;
        padding: .1em .2em;
        min-width: 40px;
        text-align: center;
        visibility: hidden;
        transform: translateX(-50%);
    }

}

.m-option-base {
    input:checked + label > .a-label-question {
        color: globals.$theme-secondary;
    }

    .a-label-question {
        color: globals.$theme-primary;
        display: inline-block;
        padding-left: 1.5em;
    }
}